@use 'variables' as v;
/** Media Queries **/
@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
  
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}
@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}
@mixin btn($colorBg, $display){
 background-color: $colorBg;
 color: v.$blanco;
 font-weight: v.$bold;
 text-decoration: none;
 padding: 1rem 3rem;
 text-align: center;
 margin-top: 2.5rem;
 display:$display;
 border: none;
 &:hover{
     background-color: darken($color: $colorBg, $amount: 3);
     cursor: pointer;
 }
}