html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

/** Media Queries **/
.dark-mode {
  background-color: #262626;
}

.dark-mode p {
  color: #FFFFFF;
}

.dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #e1e1e1;
}

.dark-mode .anuncio, .dark-mode .resumen-propiedad {
  background-color: #333333;
  border: 1px solid #333333;
}

.dark-mode .anuncio .precio, .dark-mode .resumen-propiedad .precio {
  color: #71B100;
}

.dark-mode .anuncio .color-icono, .dark-mode .resumen-propiedad .color-icono {
  filter: invert(100%);
}

.dark-mode .resumen-propiedad {
  padding: 0 2rem;
}

.dark-mode .texto-nosotros blockquote {
  color: #FFFFFF;
}

.dark-mode .formulario label, .dark-mode .formulario legend, .dark-mode .formulario p {
  color: #e1e1e1;
}

.dark-mode .formulario input:not([type=button]), .dark-mode .formulario textarea, .dark-mode .formulario select {
  background-color: #333333;
  color: #FFFFFF;
  border: 1px solid #333333;
}

.dark-mode td {
  color: #FFFFFF;
}

/** Media Queries **/
.no-webp .inicio {
  background-image: url("/public/build/img/header.jpg");
}

.webp .inicio {
  background-image: url("/public/build/img/header.webp");
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}

.header.inicio {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}
.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.contenido-header h1 {
  display: flex;
  flex-direction: column;
  color: #FFFFFF;
  text-align: left;
  font-weight: 700;
}

.contenido-header h1 span {
  font-weight: 300;
}

.derecha {
  display: none;
}

@media (min-width: 768px) {
  .derecha {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
}
.derecha .dark-mode-boton {
  width: 3rem;
  margin: 3rem 0;
  filter: invert(100%);
  cursor: pointer;
}

@media (min-width: 768px) {
  .derecha .dark-mode-boton {
    margin: 0;
    margin-bottom: 2rem;
  }
}
.barra {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column-reverse;
  justify-content: center;
  padding-top: 3rem;
  height: auto;
  transition: height 0.3s ease-in-out;
}

@media (min-width: 768px) {
  .barra {
    display: grid;
    grid-template-columns: 30% 70%;
    place-items: center;
    align-items: end;
  }
}
.navegacion {
  display: none;
  order: 2;
  opacity: 0;
  transition: opacity 1s linear;
}

@media (min-width: 768px) {
  .navegacion {
    opacity: 1;
    display: flex;
    align-items: center;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}

@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
  }

  .navegacion a:last-of-type {
    margin-right: 0;
  }
}
.navegacion a:hover {
  color: #f6a026;
}

.fijar {
  z-index: 100000000;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 2rem 5rem;
  background-color: #333333;
  box-shadow: 0 0 2rem #000000;
}

.logo-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  order: 1;
  width: 100%;
}

@media (min-width: 768px) {
  .logo-menu {
    order: initial;
  }
}
.menu-hamburguesa {
  width: 5rem;
  cursor: pointer;
}

@media (min-width: 768px) {
  .menu-hamburguesa {
    display: none;
  }
}
.mostrar {
  display: flex;
  flex-direction: column;
  align-items: center;
  opacity: 1;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icono {
  text-align: center;
}

.icono img {
  height: 10rem;
}

.icono h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .contedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.anuncio {
  background-color: #eeeeee;
  border: 1px solid #d2d2d2;
  margin-bottom: 3rem;
  height: 100%;
}

.anuncio:last-of-type {
  margin-bottom: 0;
}

.anuncio h3, .anuncio p {
  margin: 0;
}

.contenido-anuncio {
  padding: 2rem;
}

.precio {
  color: #71B100;
  font-weight: 700;
  font-size: 2.5rem;
}

.iconos-caracteristicas {
  list-style: none;
  padding: 0;
  display: flex;
  justify-content: space-evenly;
}

.iconos-caracteristicas li {
  display: flex;
}

.iconos-caracteristicas li img {
  flex: 0 0 3rem;
  margin-left: 1rem;
}

.iconos-caracteristicas li p {
  font-weight: 700;
}

.btn-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}

.btn-amarillo:hover {
  background-color: #d17e08;
  cursor: pointer;
}

.btn-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}

.btn-verde:hover {
  background-color: #67a200;
  cursor: pointer;
}

.btn-rojo-block {
  background-color: #b10e0e;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  width: 100%;
}

.btn-rojo-block:hover {
  background-color: #a30d0d;
  cursor: pointer;
}

.btn-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}

.btn-amarillo-block:hover {
  background-color: #d17e08;
  cursor: pointer;
}

.webp .grafico {
  background-image: url("/build/img/encuentra.webp");
}

.no-webp .grafico {
  background-image: url("/build/img/encuentra.jpg");
}

.grafico {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 4rem;
}

.grafico h2, .grafico p {
  color: #FFFFFF;
  margin-top: 0;
}

.grafico h2 {
  font-weight: 900;
  font-size: 4rem;
}

.grafico p {
  font-size: 2.8rem;
}

.btn {
  background-color: transparent;
  border: 1px solid #E08709;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
  color: #E08709;
  font-weight: 700;
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease-in;
  z-index: 10;
}

.btn::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: -1;
  bottom: -8rem;
  left: 0;
  background-color: #d17e08;
  transition: all 0.3s ease-in-out;
}

.btn:hover {
  color: #FFFFFF;
}

.btn:hover::after {
  bottom: 0;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
}

@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 3rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}

.texto-entrada a {
  color: #000000;
  display: block;
  margin-top: 2rem;
}

@media (min-width: 768px) {
  .texto-entrada a {
    margin-top: 0;
  }
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
}

.texto-entrada h4::after {
  content: "";
  display: block;
  width: 25rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1.2rem;
}

.informacion-meta span {
  color: #E08709;
}

.testimoniales {
  display: flex;
  flex-direction: column;
}

.testimoniales h3 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.testimoniales h3::after {
  content: "";
  background-color: #333333;
  width: 10rem;
  height: 0.3rem;
}

.testimonial {
  background-color: #71B100;
  margin-bottom: 2rem;
  padding: 2rem;
  border-radius: 2rem;
}

.testimonial:last-of-type {
  margin-bottom: 0;
}

.testimonial blockquote, .testimonial p {
  color: #FFFFFF;
  margin: 0;
  font-size: 2.4rem;
}

.testimonial blockquote {
  padding-left: 4rem;
  position: relative;
}

.testimonial blockquote::before {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  background-image: url("/build/img/comilla.svg");
  background-repeat: no-repeat;
  width: 5rem;
  height: 3rem;
  display: block;
}

.testimonial p {
  text-align: right;
  font-weight: 700;
}

@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.texto-nosotros {
  margin-top: 2rem;
}

@media (min-width: 768px) {
  .texto-nosotros {
    margin-top: 0;
  }
}
.texto-nosotros blockquote {
  margin-top: 3rem 0 0 0;
  font-weight: 700;
  font-size: 3rem;
}

.section {
  display: none;
}

.paginacion {
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media (min-width: 768px) {
  .paginacion {
    flex-direction: row;
    justify-content: space-between;
  }
}
.paginacion .btn-paginacion {
  background-color: transparent;
  border: none;
  color: #406500;
  font-size: 4rem;
  margin-bottom: 3rem;
  cursor: pointer;
  font-weight: 700;
}

.paginacion .btn-paginacion:last-of-type {
  margin-bottom: 0;
}

@media (min-width: 768px) {
  .paginacion .btn-paginacion {
    font-size: 3.5rem;
    margin-bottom: 0;
  }
}
.paginacion .btn-paginacion:hover {
  color: #71B100;
}

.paginacion .btn-paginacion.actual {
  color: #71B100;
}

.mostrar {
  display: block;
}

.formulario p {
  color: #333333;
  font-size: 1.5rem;
  margin: 2rem 0;
}

.formulario legend {
  font-size: 2rem;
}

.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}

.formulario input:not([type=button]), .formulario textarea, .formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}

.formulario textarea {
  height: 20rem;
}

.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}

.formulario input[type=radio] {
  margin: 0;
  width: auto;
}

.footer {
  background-color: #333333;
  padding: 2.5rem 0;
  text-align: center;
  margin-bottom: 0;
}

.footer .contenedor-footer {
  display: flex;
  justify-content: center;
  padding-top: 2.5rem;
}

.footer .navegacion {
  display: block;
  opacity: 1;
}

@media (min-width: 768px) {
  .footer .navegacion {
    display: flex;
  }
}
.footer p {
  color: #FFFFFF;
}

/** Media Queries **/
.alerta {
  font-size: 2rem;
  text-align: center;
  padding: 0.5rem;
  color: #FFFFFF;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
}

.alerta.error {
  background-color: #b10e0e;
}

.alerta.exito {
  background-color: #71B100;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}

table.propiedades thead {
  background-color: #71B100;
}

table.propiedades thead th {
  text-align: center;
  color: #FFFFFF;
  padding: 1rem;
}

table.propiedades td {
  text-align: center;
}

table.propiedades .imagen-tabla {
  width: 10rem;
  display: inline-block;
}

.imagen-small {
  width: 20rem;
}